<template>
  <v-autocomplete
    :item-text="itemText"
    :item-value="itemValue"
    :items="[value, ...items]"
    :label="label"
    :loading="loading"
    :search-input.sync="search"
    hide-details
    hide-no-data
    v-model="select"
  />
</template>

<script>
export default {
  props: {
    itemText: String,
    itemValue: String,
    label: String,
    searchItems: Function,
    value: Object
  },
  data: () => ({
    loading: false,
    search: null,
    items: [],
    searchTimeout: null
  }),
  computed: {
    select: {
      get () {
        return (this.value || {})[this.itemValue]
      },
      set (value) {
        const item = this.items.find(item => item[this.itemValue] === value)
        this.$emit('input', item)
      }
    }
  },
  watch: {
    search (val) {
      val && val !== (this.value || {})[this.itemText] && this.querySelections(val)
    }
  },
  methods: {
    querySelections (v) {
      window.clearTimeout(this.searchTimeout)
      this.searchTimeout = window.setTimeout(async () => {
        this.searchTimeout = null
        try {
          this.loading = true
          this.items = await this.searchItems(v)
        } catch (error) {
          console.error(error)
        } finally {
          this.loading = false
        }
      }, 600)
    }
  }
}
</script>
