<template>
  <v-container :class="{ 'px-0': $vuetify.breakpoint.mdAndDown }" v-scroll="scroll">
    <v-layout text-xs-center wrap>
      <v-flex xs12 py-5 v-if="this.xhrs.searchItems">
        <v-text-field
          solo clearable
          v-model="search"
          style="max-width: 600px; margin: 0 auto;"
          @keyup.enter="load(true)"
          @click:clear="() => { search = null; load(true) }"
        >
          <v-btn slot="append" flat color="primary" v-text="$t('search')" @click="load(true)"/>
        </v-text-field>
      </v-flex>

      <v-flex d-flex xs12 sm6 md4 lg3 xl2 v-for="(item, i) in items" :key="`card_${i18nKey}_${i}`" class="pa-2">
        <v-card>
          <v-card-title class="headline">
            <slot name="headline" :item="item"/>
          </v-card-title>
          <v-card-text class="text-xs-left">
            <slot :item="item"/>
          </v-card-text>
          <v-card-actions>
            <v-spacer/>
            <v-btn flat color="primary" v-text="$t('edit')" @click="edit(i)"/>
            <v-btn flat color="primary" v-text="$t('delete')" @click="remove(i)"/>
          </v-card-actions>
        </v-card>
      </v-flex>

      <v-flex xs12 :style="{ visibility: loading ? 'visible' : 'hidden' }" class="pa-5">
        <v-progress-circular color="primary" :indeterminate="true"/>
      </v-flex>

      <slot name="edit"
        :item="items[index] || createNewItem()" :itemCb="save"
        :show="show" :showCb="editShowCb"
      />

      <v-btn @click="edit(items.length)" color="accent" fixed dark fab bottom right>
        <v-icon>mdi-plus</v-icon>
      </v-btn>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: {
    i18nKey: String,
    idProp: String,
    newItem: Object,
    xhrs: Object
  },
  data: () => ({
    index: -1,
    items: [],
    loading: false,
    search: null,
    show: false
  }),
  computed: {
    itemsPerLoad () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 3
        case 'sm': return 6
        case 'md': return 9
        case 'lg': return 12
        case 'xl': return 18
        default: return 1
      }
    }
  },
  methods: {
    edit (i) {
      this.index = i
      this.show = true
    },
    async save (item) {
      const oldItem = this.items[this.index]
      this.$set(this.items, this.index, item)
      if (item[this.idProp] != null) {
        try {
          this.$store.commit('setWorking', true)
          await this.xhrs.putItem(JSON.parse(JSON.stringify(item)))
          this.$store.commit('setAlert', {
            type: 'success',
            text: this.$t(`xhrs.${this.i18nKey}.put.success`)
          })
        } catch (error) {
          this.$store.commit('setAlert', {
            type: 'error',
            text: this.$t(`xhrs.${this.i18nKey}.put.error`)
          })
          this.$set(
            this.items,
            this.items.indexOf(item),
            oldItem
          )
        } finally {
          this.$store.commit('setWorking', false)
        }
      } else {
        try {
          this.$store.commit('setWorking', true)
          const id = await this.xhrs.postItem(JSON.parse(JSON.stringify(item)))
          item[this.idProp] = id
          this.$store.commit('setAlert', {
            type: 'success',
            text: this.$t(`xhrs.${this.i18nKey}.post.success`)
          })
        } catch (error) {
          this.$store.commit('setAlert', {
            type: 'error',
            text: this.$t(`xhrs.${this.i18nKey}.post.error`)
          })
          this.$set(
            this.items,
            this.items.indexOf(item),
            oldItem
          )
        } finally {
          this.$store.commit('setWorking', false)
        }
      }
    },
    async remove (i) {
      if (confirm(this.$t('confirm delete'))) {
        const item = this.items.splice(i, 1)[0]
        try {
          this.$store.commit('setWorking', true)
          await this.xhrs.deleteItem(item[this.idProp])
          this.$store.commit('setAlert', {
            type: 'success',
            text: this.$t(`xhrs.${this.i18nKey}.delete.success`)
          })
        } catch (error) {
          this.items.splice(i, 0, item)
          this.$store.commit('setAlert', {
            type: 'error',
            text: this.$t(`xhrs.${this.i18nKey}.delete.error`)
          })
        } finally {
          this.$store.commit('setWorking', false)
        }
      }
    },
    async load (clear) {
      this.loading = true
      if (clear) {
        this.items = []
      }
      try {
        const items = this.search != null && this.search !== ''
          ? await this.xhrs.searchItems(this.search, this.items.length, this.itemsPerLoad)
          : await this.xhrs.getItems(this.items.length, this.itemsPerLoad)
        if (items.length) {
          this.items.push(...items)
          window.setTimeout(() => {
            this.scroll()
          })
        }
      } catch (error) {
        console.error(error)
      } finally {
        this.loading = false
      }
    },
    scroll () {
      if (
        !this.loading && (
          document.documentElement.offsetHeight ===
          document.documentElement.scrollTop + window.innerHeight
        )
      ) {
        this.load()
      }
    },
    createNewItem () {
      return JSON.parse(JSON.stringify(this.newItem))
    },
    editShowCb (val) {
      this.show = val
    }
  },
  mounted () {
    this.load()
  }
}
</script>
