// @todo: there should be an API for this
async function searchCars (search, skip, count) {
  search = search.toLowerCase()
  const { data } = await download({
    method: 'get',
    url: '/car/0/100'
  })
  const set = new Set()
  return data
    .filter(v => {
      try {
        if (
          v != null &&
          !set.has(v.carId) && (
            v.name.toLowerCase().includes(search) ||
            v.owner.name.toLowerCase().includes(search) ||
            v.serie.name.toLowerCase().includes(search) ||
            v.serviceResponsiblePerson.name.toLowerCase().includes(search)
          )
        ) {
          set.add(v.carId)
          return true
        } else {
          return false
        }
      } catch (_) {
        return false
      }
    })
    .filter((_, i) => i >= skip && i < skip + count)
}

