<template>
  <v-dialog :value="value" persistent scrollable :fullscreen="$vuetify.breakpoint.xsOnly" max-width="600px" @keydown.esc="cancel()">
    <v-card>
      <v-card-title>
        <span class="headline" v-text="headline"/>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <slot/>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer/>
        <v-btn color="primary" flat @click.native="cancel()">{{ $t('cancel') }}</v-btn>
        <v-btn color="primary" flat @click.native="save()">{{ $t('save') }}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    value: Boolean,
    headline: String
  },
  methods: {
    cancel () {
      this.$emit('input', false)
      this.$emit('cancel')
    },
    save () {
      this.$emit('input', false)
      this.$emit('save')
    }
  }
}
</script>
