<template>
  <DialogWrapper :headline="$t('car')" :value="show" @save="save" @cancel="cancel">
    <v-flex xs12>
      <v-text-field :label="$t('name')" v-model="item.name" required/>
    </v-flex>
    <v-flex xs12>
      <RemoteSelect
        v-model="item.owner"
        item-text="name"
        item-value="subjectId"
        :label="$t('owner')"
        :searchItems="searchOwners"
      />
    </v-flex>
    <v-flex xs12>
      <RemoteSelect
        v-model="item.serie"
        item-text="serieId"
        item-value="serieId"
        :label="$t('serie')"
        :searchItems="searchSeries"
      />
    </v-flex>
    <v-flex xs12>
      <RemoteSelect
        v-model="item.serviceResponsiblePerson"
        item-text="name"
        item-value="subjectId"
        :label="$t('service responsible person')"
        :searchItems="searchServiceResponsiblePersons"
      />
    </v-flex>
    <v-flex xs12>
      <v-switch :label="$t('certification')" v-model="item.certification" required/>
    </v-flex>
    <v-flex xs12>
      <v-text-field :label="$t('revision period')" v-model="item.revisionPeriod"/>
    </v-flex>
    <v-flex xs12>
      <DateTime :label="$t('last revision')" v-model="item.lastRevision"/>
    </v-flex>
    <v-flex xs12>
      <DateTime :label="$t('last ZTE')" v-model="item.lastZTE"/>
    </v-flex>
    <v-flex xs12>
      <DateTime :label="$t('last ZTL')" v-model="item.lastZTL"/>
    </v-flex>
  </DialogWrapper>
</template>

<script>
import DateTime from '@/components/DateTime'
import DialogWrapper from '@/components/DialogWrapper'
import RemoteSelect from '@/components/RemoteSelect'
import { searchOwners, searchSeries, searchServiceResponsiblePersons } from '@/axios'

export default {
  components: { DialogWrapper, DateTime, RemoteSelect },
  props: {
    show: Boolean,
    value: Object
  },
  data: () => ({
    item: {},
    searchOwners,
    searchSeries,
    searchServiceResponsiblePersons
  }),
  watch: {
    show (val) {
      if (val) {
        this.item = JSON.parse(JSON.stringify(this.value))
      }
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
      this.$emit('show', false)
    },
    save () {
      this.$emit('input', this.item)
      this.$emit('show', false)
    }
  }
}
</script>
